.create-note-modal-container {
    text-align: center;
    position: relative;
}

.create-note-modal-title-field {
    padding-right: 5rem;
    max-width: 40%;
}

.create-note-modal-small-field {
    max-width: 50%;
    padding-right: 1rem;
}

.create-note-modal-xxsmall-field {
    max-width: 15%;
    padding-right: 1rem;
}

.create-note-footer-container {
    display: flex;
    justify-content: space-between;
}

.create-note-inner-container {
    display: flex;
    flex: 0 1 45%;
}
.create-note-delete {
    background-color: $red;
    flex: 0 1 20%;
}

.student-creation-form-header-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;

    &--right {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
    }
}

.note-creation-form-exit {
    position: absolute;
    top : 0.2rem;
    right : 0.6rem;

    width: 1.8rem;
    height: 1.8rem;
}

.note-creation-form-exit:hover {
    color: $blueGrotto;
    cursor: pointer;
}

.note-creation-editor-header {
    padding-top: 2rem;
}

.note-creation-editor {
    height: 25rem !important;
    padding-bottom: 2rem;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.create-note-modal-xsmall-field {
    padding-right: 1rem;
}