.student-details-container {
    color: $blueNavy;
    max-height: 30vh;
    background-color: $white;

    overflow: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;
    text-align: start;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 20px;
}

.student-details-delete {
    padding-left: 1rem;
}
.student-details-delete:hover {
    color: $blueGrotto;
    cursor: pointer;
}

.student-container--notes {
    max-height: 55vh;
    padding: 20px;
}

.student-details-container::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.student-seemore-details {
    text-decoration: none;
    padding-right: 20px;
}

.student-seemore-details:hover {
    text-decoration: underline;
}

.student-details-description {
    color: $blueNavy;
    padding-bottom: 10px;
    min-height: 70px;
    width:100%;
}

#student-details-save {
    display: none;
}
.student-details-groups {
    display: flex;
    justify-content: flex-start;
}

.student-details-section-title {
    font-size: 24px;
    color: $lightGrey;

    display:flex;
    justify-content: space-between;
}

.student-details-overflow-cell {
    text-overflow: ellipsis;
}

.student-notes-header-container {
    display:flex;
    justify-content: space-between;;
}

.student-details-notes-add-icon {
    width: 30px;
    height: 30px;
}

.student-details-notes-add-icon:hover {
    color: $blueGrotto;
    cursor: pointer;
}

.input-date {
    font-family: $fontPoppins;
    font-weight: 500;
    font-size: 15px;
    line-height: 1.5;
    color: $lightGrey;
    display: block;
    width: 100%;
    background: $coldWhite;
    height: 50px;
    border-radius: 25px;
    padding: 0 30px 0 38px;
}