* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html {
    height: 100%;
    font-family: $fontPoppins, sans-serif;
    font-weight: normal;
}

a {
    font-family: $fontPoppins;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.7;
    color: $regularGrey;
    margin: 0;
    transition: all 0.4s;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
}

a:focus {
    outline: none !important;
}

a:hover {
    text-decoration: none;
    color: $blueGrotto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    font-family: $fontPoppins;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.7;
    color: $regularGrey;
    margin: 0;
}

ul,
li {
    margin: 0;
    list-style-type: none;
}

input {
    outline: none;
    border: none;
}

textarea {
    outline: none;
    border: none;
}

textarea:focus,
input:focus {
    border-color: transparent !important;
}

input:focus::-webkit-input-placeholder {
    color: transparent;
}
input:focus:-moz-placeholder {
    color: transparent;
}
input:focus::-moz-placeholder {
    color: transparent;
}
input:focus:-ms-input-placeholder {
    color: transparent;
}

textarea:focus::-webkit-input-placeholder {
    color: transparent;
}
textarea:focus:-moz-placeholder {
    color: transparent;
}
textarea:focus::-moz-placeholder {
    color: transparent;
}
textarea:focus:-ms-input-placeholder {
    color: transparent;
}

input::-webkit-input-placeholder {
    color: $lightGrey;
}
input:-moz-placeholder {
    color: $lightGrey;
}
input::-moz-placeholder {
    color: $lightGrey;
}
input:-ms-input-placeholder {
    color: $lightGrey;
}

textarea::-webkit-input-placeholder {
    color: $lightGrey;
}
textarea:-moz-placeholder {
    color: $lightGrey;
}
textarea::-moz-placeholder {
    color: $lightGrey;
}
textarea:-ms-input-placeholder {
    color: $lightGrey;
}

button {
    outline: none !important;
    border: none;
    background: transparent;
}

button:hover {
    cursor: pointer;
}

iframe {
    border: none !important;
}

.light-text {
    font-family: $fontPoppins;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.5;
    color: $lightGrey;
}

.regular-text {
    font-family: $fontPoppins;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.5;
    color: $regularGrey;
}

.small-text {
    font-size: 10px;
}

.limiter {
    width: 100%;
    margin: 0 auto;
}

.container-auth {
    width: 100%;
    display: flex;

    overflow: auto;
    height: 100%;

    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;

    background: $babyBlue;
    background: -webkit-linear-gradient(-135deg, $blueNavy, $blueGreen);
    background: -o-linear-gradient(-135deg, $blueNavy, $blueGreen);
    background: -moz-linear-gradient(-135deg, $blueNavy, $blueGreen);
    background: linear-gradient(-135deg, $blueNavy, $blueGreen);
}

.auth-subtitle {
    padding-top: 12px;
}

.auth-pic {
    flex : 1 0 50%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    @include respond-to($desktop--small) {
        display: none;
    }

}

.auth-form-subtitle {
    font-family: $fontPoppins;
    font-weight: bold;
    font-size: 16px;
    color: #333333;
    line-height: 1.2;
    text-align: center;

    width: 100%;
    display: block;
    padding-bottom: 10px;
}

.auth-form-title {
    font-family: $fontPoppins;
    font-weight: bold;
    font-size: 24px;
    color: #333333;
    line-height: 1.2;
    text-align: center;

    width: 100%;
    display: block;
    padding-bottom: 54px;

    @include respond-to($desktop--large) {
        margin-top: 2rem;
    }
}

.wrap-input {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
}

.input {
    font-family: $fontPoppins;
    font-weight: 500;
    font-size: 15px;
    line-height: 1.5;
    color: $regularGrey;

    display: block;
    width: 100%;
    background: $coldWhite;
    height: 50px;
    border-radius: 25px;
    padding: 0 30px 0 68px;

    &--small {
        font-size: 13px;
    }
}

.focus-input {
    display: block;
    position: absolute;
    border-radius: 25px;
    bottom: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    box-shadow: 0px 0px 0px 0px;
    color: $blueGrotto;
}

.input:focus + .focus-input {
    z-index: 1;
    -webkit-animation: anim-shadow 0.5s ease-in-out forwards;
    animation: anim-shadow 0.5s ease-in-out forwards;
}

@-webkit-keyframes anim-shadow {
    to {
        z-index: -1;
        box-shadow: 0px 0px 70px 25px;
        opacity: 0;
    }
}

@keyframes anim-shadow {
    to {
        z-index: -1;
        box-shadow: 0px 0px 70px 25px;
        opacity: 0;
    }
}

.symbol-input {
    font-size: 15px;

    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    position: absolute;
    border-radius: 25px;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 35px;
    pointer-events: none;
    color: $regularGrey;

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
    transition: all 0.4s;
}

.input:focus + .focus-input + .symbol-input {
    color: $blueGrotto;
    padding-left: 28px;
}

.container-auth-form-btn {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
}

.auth-form-btn {
    font-family: $fontMontserratBold;
    font-size: 15px;
    line-height: 1.5;
    color: #fff;
    text-transform: uppercase;

    width: 100%;
    height: 50px;
    border-radius: 25px;
    background: $blueGrotto;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 25px;

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
    transition: all 0.4s;
}

.auth-form-btn:hover {
    background: $blueNavy;
}

#auth-error {
    color: $red;
    display: none;
}

.auth-form {
    flex: 1 0 30%;
    @include respond-to($desktop) {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
}

.wrap-auth {
    width: 60%;
    background: #fff;
    border-radius: 10px;
    overflow: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;

    padding: 6rem;

    flex-direction: row;
    @include respond-to($desktop) {
        flex-direction: column;
    }

    &--column {
        flex-direction: column;
    }
}

.validate-input {
    position: relative;
}

.alert-validate::before {
    content: attr(data-validate);
    position: absolute;
    max-width: 70%;
    background-color: $white;
    border: 1px solid $darkRed;
    border-radius: 13px;
    padding: 4px 25px 4px 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 8px;
    pointer-events: none;

    font-family: $fontPoppins;
    font-weight: 500;
    color: $darkRed;
    font-size: 13px;
    line-height: 1.4;
    text-align: left;

    visibility: hidden;
    opacity: 0;

    -webkit-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    -moz-transition: opacity 0.4s;
    transition: opacity 0.4s;
}

.alert-validate::after {
    content: '\f06a';
    font-family: FontAwesome, serif;
    display: block;
    position: absolute;
    color: $darkRed;
    font-size: 15px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 13px;
}

.alert-validate:hover:before {
    visibility: visible;
    opacity: 1;
}

@include respond-to($desktop) {
    .alert-validate::before {
        visibility: visible;
        opacity: 1;
    }
}

.text-center {
    text-align: center;
}
