.group-details-container {
    color: $blueNavy;
    max-height: 30vh;
    background-color: $white;

    overflow: hidden;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;
    text-align: start;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 20px;
}

.group-details-section-title {
    font-size: 24px;
    color: $lightGrey;

    display:flex;
    justify-content: space-between;
}

.group-details-delete {
    padding-left: 1rem;

    &:hover {
        color: $blueGrotto !important;
        cursor: pointer;
    }
}

.group-details-add {
    padding-left: 1rem;
    width: 25px;
    height: 25px;

    &:hover {
        color: $blueGrotto !important;
        cursor: pointer;
    }
}

.group-input-element {
    max-width: 20%;
    margin: 1rem 0;
}

.group-add-container {
    display: flex;
    justify-content: flex-start;
    align-items:center;
}

#group-details-save {
    display: none;
}

.group-details-description {
    color: $blueNavy;
    border-radius: 25px;
    overflow: hidden;
    height: 85%;
    width: 100%;
    padding: 10px;
    resize: none;
}

.group-container--notes {
    max-height: 55vh;
    padding: 20px;
}
