.sidebar-logo {
    margin-bottom: 15px;
}

.side-navigation-panel
    .side-navigation-panel-select
    .side-navigation-panel-select-wrap
    .side-navigation-panel-select-option:hover {
    background-color: $babyBlue;
    border-color: $blueNavy;
    color: $blueGrotto;
}

.side-navigation-panel
    .side-navigation-panel-select
    .side-navigation-panel-select-inner
    .side-navigation-panel-select-inner-wrap
    .side-navigation-panel-select-inner-option-selected {
    background-color: unset;
    border-color: unset;
    color: unset;
}

.side-navigation-panel
    .side-navigation-panel-select
    .side-navigation-panel-select-wrap
    .side-navigation-panel-select-option-selected {
    background-color: unset;
    border-color: unset;
    color: unset;
}
.sidebar {
    background-color: $white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
    padding-bottom: 2rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@include respond-to($desktop--small) {
    .sidebar-logo {
        display: none;
    }
    .side-navigation-panel-select-option-text {
        display: none;
    }
}

.side-navigation-panel-select-option-selected {
    color: $blueGrotto !important;
}

.side-navigation-panel .side-navigation-panel-select .side-navigation-panel-select-wrap .side-navigation-panel-select-option-selected {
    border-color: unset !important;
}

.side-navigation-panel .side-navigation-panel-select .side-navigation-panel-select-wrap .side-navigation-panel-select-option:hover {
    border-color: $blueGrotto !important;
}