.dashboard-container {
    color: $white;
}

.dashboard-content {
    margin-top: 20px;
    margin-right: 20px;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.dashboard-content-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    @include respond-to($desktop--large) {
        flex-direction: column;
    }
}

.dashboard-upper-section {
    border-radius: 25px;
    color: $blueNavy;
    height: 40vh;
    width: 50%;
    background-color: $white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    position: relative;
    overflow: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;

    @include respond-to($desktop--large) {
        width: 100%;
        margin-top: 2rem;
    }
}

.dashboard-upper-section::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

#first-upper-section {
    margin-right: 50px;
}

.dashboard-table {
    width: 98%;
    margin-left: 1%;
}

table tr:hover {
    cursor: pointer;
}

.dashboard-bottom-section {
    margin-top: 50px;
    border-radius: 25px;
    color: $blueNavy;
    height: 40vh;
    width: 100%;
    background-color: $white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    position: relative;
    overflow: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;

    @include respond-to($desktop--large) {
        display: none;
    }
}

.dashboard-bottom-section::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

.dashboard-groups {
    display: none;
    text-align: center;
    color: $red;
}

.dashboard-students {
    display: none;
    text-align: center;
    color: $red;
}
.dashboard-notes-error {
    display: none;
    text-align: center;
    color: $red;
}

.dashboard-section-title {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: large;
}

.dashboard-section-subtitle {
    font-size: small;
    font-weight: normal;
}

.dashboard-section-cta {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border-style: hidden;

    overflow: scroll;
    -ms-overflow-style: unset; /* for Internet Explorer, Edge */
    scrollbar-width: unset;
}
table::-webkit-scrollbar {
    display: unset; /* for Chrome, Safari, and Opera */
}

.table_color_column {
    max-width: 28px;
}

.table_color_column_square {
    height: 20px;
    width: 20px;
    border-radius: 10px;
}

td,
th {
    border: 1px solid #ddd;
    padding: 8px;
}

tr {
    background-color: #f2f2f2;
}

tr:hover {
    background-color: #ddd;
}

th {
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: left;
    background-color: $blueGrotto;
    color: white;
}
