.list-container {
    color: $blueNavy;
    height: 60vh;
    background-color: $white;
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 25px;

    overflow: scroll;
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;
    text-align: start;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.list {
    text-align: center;
}

.list-creation-title {
    margin-bottom: 1rem;
}

.list-add {
    width: 40% !important;
    display: unset !important;
    margin-top: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.list-container::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}
.list-seemore {
    display: flex;
    justify-content: center;
}

.list-header {
    color: $white;
}

.Collapsible__trigger:hover {
    text-decoration: underline;
}

.Collapsible__trigger {
    cursor: pointer;
    display: block;
    text-decoration: none;
    position: relative;
    padding: 20px;
}

.list-collapsible {
    padding-left: 20px;
    padding-right: 20px;
	padding-top: 20px;

}

.list-link {
	display:flex;
	flex-direction: column;
	justify-content: space-between;
}

.list-link a {
	padding-bottom: 10px;
	padding-top: 10px;

	text-decoration: none;
}

.list-link a:hover {
	text-decoration: underline;
}