.signup-connect {
    padding-top: 40px;
    margin: auto;
}

.signUp-form {
    flex-wrap: wrap;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    width: 100%;
}

.container-signup-form-btn {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.signup-modal {
    color: $blueNavy;
}

@include respond-to($desktop--small) {
    .signup-nomargin {
        margin: unset !important;
    }
}